
import cl from './Button.module.css'


export const Button = ({children = 'Hi', type, size = 'medium', variant='square'})=>{
    const mainCN = cl.button + " " + cl[size] + " " + cl[variant]

    return(
        <button
        className={[cl.button, cl.size, cl.variant].join}
        type={type}
        >
            {children}
        </button>
    )
}